<template>
  <div class="wrapperCenter">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item
        >来粉统计 / 控制台 / 项目设置 / 绑定域名</el-breadcrumb-item
      >
    </el-breadcrumb>
    <div class="top">
      <div class="btnBox">
        <el-button
          @click="deletaFn"
          type="danger"
          :disabled="deleteArr.length < 1"
          >删除</el-button
        >
        <el-button style="float: right" @click="addDoMainDialog" type="primary"
          >绑定域名</el-button
        >
      </div>
      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        :class="{ nodataP: tableData.length < 5 }"
        @selection-change="handleSelectionChange"
        :header-cell-style="{ background: '#F3F4F7', color: '#555' }"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="编号" prop="id">
          <!-- <template slot-scope="scope">{{ scope.row.date }}</template> -->
        </el-table-column>
        <el-table-column
          prop="domain_url"
          label="域名"
          width="120"
        ></el-table-column>
        <el-table-column prop="put_channel" label="渠道类型"></el-table-column>
        <el-table-column
          prop="change_type_name"
          label="转化类型"
        ></el-table-column>
        <el-table-column
          prop="change_child_name"
          label="子类型"
        ></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <span @click="getCode(scope.row)">获取代码</span>
            <span @click="editor(scope.row)">编辑</span>
          </template>
        </el-table-column>
        <div slot="empty" class="nodata">
          <img src="@/assets/noHaveData.jpg" alt="" />
          <p>暂无数据</p>
        </div>
      </el-table>
      <el-pagination
        v-if="tableData.length > 0"
        background
        layout="prev, pager, next"
        :total="total"
        :current-page.sync="page.page"
        :page-size="page.page_size"
        @current-change="currentChange"
      ></el-pagination>
    </div>

    <!-- 添加域名 -->
    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      width="30%"
      class="dialo222222222g"
      :before-close="initDialog"
    >
      <div v-if="dialogTitle != '获取代码'">
        <el-form
          :model="addDoMain"
          :rules="addDoMainRules"
          ref="addDoMain"
          label-width="100px"
        >
          <el-form-item label="绑定域名" prop="domain_url">
            <el-input v-model="addDoMain.domain_url"></el-input>
          </el-form-item>
          <el-form-item label="投放渠道" prop="put_channel">
            <el-select v-model="addDoMain.put_channel" placeholder="请选择">
              <el-option
                v-for="item in channel"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="转化类型" prop="change_info">
            <el-select
              v-model="addDoMain.change_info"
              placeholder="请选择"
              multiple
            >
              <el-option
                v-for="item in changeTypeArr"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="子类型" prop="change_child_info">
            <el-select
              v-model="addDoMain.change_child_info"
              multiple
              placeholder="请选择"
            >
              <el-option
                v-for="item in childT"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <p style="margin-bottom: 12px">
          注意：域名无需添加协议头http://或https://
          <br />举例：域名为http://www.baidu.com，只需填写www.baidu.com即可
        </p>
        <span slot="footer" class="dialog-footer">
          <el-button type="warning" @click="reset">重置</el-button>
          <el-button
            type="primary"
            @click="submit"
            v-loading.lock="fullscreenLoading"
            >{{ dialogTitle == "编辑" ? "修 改" : "添 加" }}</el-button
          >
        </span>
      </div>

      <div v-else class="codeJsBox">
        <el-input
          id="codeJS"
          type="textarea"
          :rows="2"
          placeholder="请输入内容"
          v-model="getCodeRes"
        ></el-input>
        <span slot="footer" class="dialog-footer">
          <el-button style="margin-top: 20px" type="primary" @click="copyCode"
            >点击复制</el-button
          >
        </span>
      </div>
      <!--  -->
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogTitle: "添加域名",
      fullscreenLoading: false,
      tableData: [],
      page: {
        page: 1,
        page_size: 10,
      },
      total: 0,
      channel: [],
      dialogVisible: false,
      addDoMain: {
        domain_url: "",
        put_channel: "",
        change_info: "",
        change_child_info: [],
      },
      addDoMainRules: {
        domain_url: [
          { required: true, message: "请输入域名", trigger: "blur" },
        ],
        put_channel: [
          { required: true, message: "请选择投放渠道", trigger: "blur" },
        ],
        change_info: [
          { required: true, message: "请选择转化类型", trigger: "blur" },
        ],
        change_child_info: [
          { required: true, message: "请选择子类型", trigger: "blur" },
        ],
      },
      childT: [
        { id: 1, name: "表单提交" },
        { id: 2, name: "拨打电话" },
        { id: 3, name: "点击客服" },
      ],
      deleteArr: [],
      getCodeRes: "",
      deletaIds: [],
      changeTypeArr: [
        { value: "1", label: "点击click" },
        { value: "2", label: "复制" },
      ],
    };
  },
  mounted() {
    this.getData();

    //获取渠道
    this.axios.get("/v1/config/Channel/list").then((res) => {
      this.channel = res.data.result;
    });
  },
  methods: {
    deletaFn() {
      if (this.deleteArr.length < 1) {
        return;
      }
      this.axios
        .post("/v1/site_js/Site_delete/index", { ids: this.deleteArr })
        .then((res) => {
          if (res.data.code == 200) {
            this.$message.success("删除成功");
            this.page.page = 1;
          } else {
            this.$message.error(res.data.message);
          }
          this.getData();
        });
    },
    copyCode() {
      var codeJS = document.getElementById("codeJS");
      codeJS.select(); // 选择对象
      document.execCommand("Copy"); // 执行浏览器复制命令
      this.$message.success("复制成功");
      this.initDialog();
    },
    editor(item) {
      this.dialogTitle = "编辑";
      this.dialogVisible = true;
      item.change_info = JSON.parse(item.change_info);
      this.addDoMain = JSON.parse(JSON.stringify(item));
      this.addDoMain.id = item.id;
    },
    reset() {
      this.$refs["addDoMain"].resetFields();
      this.addDoMain = {
        domain_url: "",
        put_channel: "",
        change_info: "",
        change_child_info: [],
      };
    },
    getCode(item) {
      this.dialogTitle = "获取代码";
      this.dialogVisible = true;
      this.axios
        .get("/v1/site_js/Site_link/index/web_id/" + item.id)
        .then((res) => {
          if (res.data.code == 200) {
            this.getCodeRes = `<script src="${res.data.result.js_code_url}"></scpipt>`;
          } else {
            this.$message.error(res.data.message);
          }
        });
    },
    initDialog() {
      this.dialogVisible = false;
      this.addDoMain = {
        domain_url: "",
        put_channel: "",
        change_info: "",
        change_child_info: [],
      };
      this.page.page = 1;
      this.getData();
      delete this.addDoMain.id;
      this.getCodeRes = "";
    },
    submit() {
      this.$refs["addDoMain"].validate((valid) => {
        if (valid) {
          if (this.dialogTitle == "添加域名") {
            this.axios
              .post("/v1/site_js/Site_add/index", this.addDoMain)
              .then((res) => {
                if (res.data.code == 200) {
                  this.$message.success("添加成功");
                  this.dialogVisible = false;
                  this.initDialog();
                } else {
                  this.$message.error(res.data.message);
                }
              });
          } else if (this.dialogTitle == "编辑") {
            this.addDoMain.id = this.axios
              .post("/v1/site_js/Site_update/index", this.addDoMain)
              .then((res) => {
                if (res.data.code == 200) {
                  this.$message.success("修改成功");
                  this.initDialog();
                } else {
                  this.$message.error(res.data.message);
                }
              });
          }
        } else {
          return;
        }
      });
    },
    addDoMainDialog() {
      this.dialogVisible = true;
      this.dialogTitle = "添加域名";
    },
    handleSelectionChange(item) {
      let ids = [];
      item.forEach((e, i) => {
        ids.push(e.id);
      });
      this.deleteArr = ids;
    },
    currentChange(val) {
      this.page.page = val;
      this.getData();
    },
    getData() {
      this.axios
        .get(
          "/v1/site_js/Site_list/index/page/" +
            this.page.page +
            "/page_size/" +
            this.page.page_size
        )
        .then((res) => {
          if (res.data.code == 200) {
            this.tableData = res.data.result.list;
            this.total = res.data.result.page_info.total;
            res.data.result.list.forEach((e) => {
              let c = this.channel;
              for (let i = 0; i < c.length; i++) {
                if (c[i].id == e.put_channel) {
                  e.put_channel = c[i].name;
                }
              }
              // e.change_info = e.change_info == "1" ? "点击click" : "复制";

              let changeStr = "";
              let changeT = e.change_info ? JSON.parse(e.change_info) : [];
              let changeA = this.changeTypeArr;
              changeA.forEach((item, i) => {
                for (let j = 0; j < changeT.length; j++) {
                  if (item.value == changeT[j]) {
                    changeStr += item.label + ",";
                  }
                }
              });
              e.change_type_name = changeStr.replace(/,$/g, "");

              let str = "";
              this.childT.forEach((item, i) => {
                e.change_child_info.forEach((a) => {
                  if (item.id == a) {
                    str += item.name + ",";
                  }
                });
              });
              e.change_child_name = str.replace(/,$/g, "");
            });
          } else {
            this.$message.error(res.data.message);
          }
        });
    },
  },
};
</script>
<style lang="less">
.codeJsBox {
  textarea {
    min-height: 260px !important;
  }
}
</style>

<style lang="less" scoped>
.btnBox {
  margin: 30px 0 20px;
  & > * {
    margin: 0 20px;
  }
}
.el-select {
  width: 100%;
}
.cell span {
  margin: 0 6px;
  color: #409eff;
  cursor: pointer;
  &:hover {
    color: #0e63b9;
  }
}
.nodataP {
  margin-bottom: 250px;
}
</style>